import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Globe, MapPin, Flag, School, Users, ChevronDown, ChevronUp, Book, Newspaper, Mail, ArrowRight, Calendar } from 'lucide-react';
import Navbar from '../components/Navbar';

const HomePage = () => {
  const [theme, setTheme] = useState('light');

  useEffect(() => {
    document.body.className = theme;
  }, [theme]);

  const toggleTheme = () => {
    setTheme(prevTheme => prevTheme === 'light' ? 'dark' : 'light');
  };

  return (
    <div className={`min-h-screen ${theme === 'dark' ? 'bg-gray-900 text-white' : 'bg-gray-100 text-gray-900'}`}>
      <Navbar theme={theme} toggleTheme={toggleTheme} />
      <main className="space-y-20">
        <HeroSection theme={theme} />
        <FeaturedInitiatives theme={theme} />
        <LatestNews theme={theme} />
        <PublicationsAndReports theme={theme} />
        <InteractiveRightsMap theme={theme} />
        <UpcomingEvents theme={theme} />
        <StudentCouncilReforms theme={theme} />
        <Newsletter theme={theme} />
        <GetInvolved theme={theme} />
      </main>
    </div>
  );
};

const HeroSection = ({ theme }) => (
  <section className="relative h-screen flex items-center justify-center overflow-hidden">
    <div 
      className="absolute inset-0 z-0 bg-cover bg-center"
      style={{
        backgroundImage: "url('https://images.unsplash.com/photo-1560252829-804f1aedf1be?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80')",
        filter: theme === 'dark' ? 'brightness(0.4)' : 'brightness(0.7)'
      }}
    ></div>
    <div className="relative z-10 text-center text-white max-w-3xl mx-auto px-4">
      <motion.h1 
        className="text-5xl md:text-7xl font-bold mb-6 text-shadow-lg"
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8 }}
      >
         
      </motion.h1>
      <motion.p 
        className="text-xl md:text-2xl mb-10 text-shadow"
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, delay: 0.2 }}
      >
        
      </motion.p>
      <motion.div
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, delay: 0.4 }}
      >
        <Link 
          to="/about" 
          className="bg-purple-600 text-white px-8 py-4 rounded-full font-semibold text-lg shadow-lg hover:bg-purple-700 transition duration-300 inline-flex items-center"
        >
           <ArrowRight className="ml-2" />
        </Link>
      </motion.div>
    </div>
  </section>
);

const FeaturedInitiatives = ({ theme }) => {
  const initiatives = [
    { 
      icon: Globe, 
      title: "", 
      description: "",
      content: ""
    },
    { 
      icon: MapPin, 
      title: "", 
      description: "",
      content: ""
    },
    { 
      icon: Flag, 
      title: "", 
      description: "",
      content: "100"
    },
    { 
      icon: School, 
      title: "", 
      description: "",
      content: "500"
    }
  ];

  return (
    <section className="py-20 bg-gradient-to-b from-purple-600 to-purple-800">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold mb-16 text-center text-white"></h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {initiatives.map((initiative, index) => (
            <motion.div
              key={index}
              whileHover={{ scale: 1.03 }}
              className="bg-white rounded-lg shadow-xl p-6 flex flex-col justify-between"
            >
              <div>
                <initiative.icon size={48} className="text-purple-600 mb-4" />
                <h3 className="text-2xl font-semibold mb-3 text-gray-800">{initiative.title}</h3>
                <p className="text-gray-600 mb-4">{initiative.description}</p>
                <p className="text-gray-700 text-sm">{initiative.content}</p>
              </div>
              <Link to={`/initiatives/${initiative.title}`} className="mt-4 inline-flex items-center text-purple-600 hover:text-purple-700">
                 <ArrowRight size={16} className="ml-1" />
              </Link>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
};

