const InteractiveRightsMap = ({ theme }) => {
  const [activeRegion, setActiveRegion] = useState(null);
  const regions = [
    { 
      name: "", 
      rights: ["", "", ""],
      description: "",
      initiatives: [
        "",
        "",
        ""
      ]
    },
    { 
      name: "", 
      rights: ["", "", ""],
      description: "",
      initiatives: [
        "",
        "",
        ""
      ]
    },
    { 
      name: "", 
      rights: ["", "", ""],
      description: "",
      initiatives: [
        "",
        "",
        ""
      ]
    },
    { 
      name: "", 
      rights: ["LGBTQ+", "", ""],
      description: "LGBTQ+",
      initiatives: [
        "LGBTQ+",
        "",
        ""
      ]
    }
  ];

  return (
    <section className="py-20">
      <div className="container mx-auto px-4">
        <h2 className={`text-4xl font-bold mb-16 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
          
        </h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <div className="space-y-4">
            {regions.map((region, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.02 }}
                onClick={() => setActiveRegion(activeRegion === region ? null : region)}
                className={`p-4 rounded-lg shadow-lg cursor-pointer ${
                  theme === 'dark' ? 'bg-gray-800' : 'bg-white'
                } ${activeRegion === region ? 'ring-2 ring-purple-600' : ''}`}
              >
                <h3 className={`text-lg font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>{region.name}</h3>
                <p className={`text-sm ${theme === 'dark' ? 'text-gray-400' : 'text-gray-600'}`}>{region.description}</p>
              </motion.div>
            ))}
          </div>
          <AnimatePresence>
            {activeRegion && (
              <motion.div
                initial={{ opacity: 0, x: 50 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: 50 }}
                className={`p-6 rounded-lg shadow-lg ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}
              >
                <h3 className={`text-2xl font-semibold mb-4 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>{activeRegion.name}</h3>
                <ul className={`list-disc list-inside mb-4 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                  {activeRegion.rights.map((right, idx) => (
                    <li key={idx} className="mb-2">{right}</li>
                  ))}
                </ul>
                <h4 className={`text-lg font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>:</h4>
                <ul className={`list-decimal list-inside ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                  {activeRegion.initiatives.map((initiative, idx) => (
                    <li key={idx} className="mb-2">{initiative}</li>
                  ))}
                </ul>
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      </div>
    </section>
  );
};

