const StudentCouncilReforms = ({ theme }) => {
  const [expandedReform, setExpandedReform] = useState(null);
  const reforms = [
    { 
      title: "", 
      description: "",
      progress: "",
      details: "",
      keyPoints: [
        "",
        "",
        "",
        ""
      ]
    },
    { 
      title: "", 
      description: "",
      progress: "",
      details: "",
      keyPoints: [
        "",
        "",
        "",
        ""
      ]
    },
    { 
      title: "", 
      description: "",
      progress: "",
      details: "",
      keyPoints: [
        "",
        "",
        "",
        ""
      ]
    }
  ];

  return (
    <section className="py-20 bg-gradient-to-b from-purple-50 to-purple-100 dark:from-gray-800 dark:to-gray-900">
      <div className="container mx-auto px-4">
        <h2 className={`text-4xl font-bold mb-16 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
          
        </h2>
        <div className="space-y-6">
          {reforms.map((reform, index) => (
            <motion.div
              key={index}
              className={`p-6 rounded-lg shadow-lg ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}
            >
              <div 
                className="flex justify-between items-center cursor-pointer" 
                onClick={() => setExpandedReform(expandedReform === index ? null : index)}
              >
                <div>
                  <h3 className={`text-xl font-semibold ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>{reform.title}</h3>
                  <p className={`mt-1 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>{reform.description}</p>
                </div>
                <div className="flex items-center">
                  <span className={`mr-2 px-3 py-1 rounded-full text-sm ${theme === 'dark' ? 'bg-purple-700 text-purple-200' : 'bg-purple-100 text-purple-700'}`}>
                    {reform.progress}
                  </span>
                  {expandedReform === index ? <ChevronUp /> : <ChevronDown />}
                </div>
              </div>
              <AnimatePresence>
                {expandedReform === index && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: 'auto' }}
                    exit={{ opacity: 0, height: 0 }}
                    className="mt-4"
                  >
                    <p className={`mb-4 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>{reform.details}</p>
                    <h4 className={`font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}></h4>
                    <ul className={`list-disc list-inside ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                      {reform.keyPoints.map((point, idx) => (
                        <li key={idx} className="mb-1">{point}</li>
                      ))}
                    </ul>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
};

const Newsletter = ({ theme }) => {
  const [email, setEmail] = useState('');
  const [subscribed, setSubscribed] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    // 
    console.log('Subscribed:', email);
    setSubscribed(true);
    setEmail('');
  };

  return (
    <section className={`py-20 ${theme === 'dark' ? 'bg-gray-800' : 'bg-gray-100'}`}>
      <div className="container mx-auto px-4">
        <h2 className={`text-4xl font-bold mb-8 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
          
        </h2>
        <p className={`text-center mb-8 max-w-2xl mx-auto ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
          
        </p>
        {subscribed ? (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            className={`text-center p-4 rounded-lg ${theme === 'dark' ? 'bg-green-800 text-green-200' : 'bg-green-100 text-green-800'}`}
          >
            
          </motion.div>
        ) : (
          <form onSubmit={handleSubmit} className="max-w-md mx-auto">
            <div className="flex">
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=""
                className="flex-grow px-4 py-2 rounded-l-full focus:outline-none focus:ring-2 focus:ring-purple-600"
                required
              />
              <button
                type="submit"
                className="bg-purple-600 text-white px-6 py-2 rounded-r-full hover:bg-purple-700 transition duration-300"
              >
                 <Mail className="inline ml-1" size={18} />
              </button>
            </div>
          </form>
        )}
      </div>
    </section>
  );
};

const GetInvolved = ({ theme }) => {
  const opportunities = [
    { 
      title: "", 
      description: "",
      details: "",
      icon: Users
    },
    { 
      title: "", 
      description: "",
      details: "",
      icon: Book
    },
    { 
      title: "", 
      description: "",
      details: "",
      icon: Newspaper
    },
    { 
      title: "", 
      description: "",
      details: "",
      icon: Globe
    }
  ];

  return (
    <section className="py-20 bg-gradient-to-b from-purple-100 to-purple-200 dark:from-gray-900 dark:to-gray-800">
      <div className="container mx-auto px-4">
        <h2 className={`text-4xl font-bold mb-16 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
          
        </h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {opportunities.map((opportunity, index) => (
            <motion.div
              key={index}
              whileHover={{ scale: 1.05 }}
              className={`p-6 rounded-lg shadow-lg ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}
            >
              <div className="flex items-start">
                <opportunity.icon className={`mr-4 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} size={24} />
                <div>
                  <h3 className={`text-xl font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>{opportunity.title}</h3>
                  <p className={`mb-4 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>{opportunity.description}</p>
                  <p className={`text-sm ${theme === 'dark' ? 'text-gray-400' : 'text-gray-500'}`}>{opportunity.details}</p>
                </div>
              </div>
            </motion.div>
          ))}
        </div>
        <div className="text-center mt-12">
          <Link
            to="/get-involved"
            className="bg-purple-600 text-white px-8 py-3 rounded-full font-semibold text-lg shadow-lg hover:bg-purple-700 transition duration-300 inline-flex items-center"
          >
             <ArrowRight className="ml-2" />
          </Link>
        </div>
      </div>
    </section>
  );
};



export default HomePage;