import React, { useEffect, useRef, useState } from 'react';
import { motion, useViewportScroll, useTransform, useSpring, useInView, AnimatePresence } from 'framer-motion';
import { Award, BookOpen, Users, Briefcase, ChevronDown, HeartHandshake, Scale, Globe, ArrowRight, ChevronUp,CheckCircle, Zap } from 'lucide-react';

const AboutPage = () => {
  const { scrollYProgress } = useViewportScroll();
  const springScrollYProgress = useSpring(scrollYProgress);

  return (
    <div className="bg-gradient-to-b from-purple-100 to-white min-h-screen">
      <HeroSection progress={springScrollYProgress} />
      <DirectorInfo />
      <DepartmentIntro />
      <CoreConcepts />
      <OrganizationStructure />
      <BusinessIntroduction />
      <Timeline />
    </div>
  );
};

const HeroSection = ({ progress }) => {
  const y = useTransform(progress, [0, 1], ['0%', '50%']);

  return (
    <motion.div 
      className="relative h-screen flex items-center justify-center overflow-hidden"
      style={{ y }}
    >
      <div className="absolute inset-0 z-0">
        <motion.div
          className="w-full h-full bg-cover bg-center"
          style={{
            backgroundImage: "url('https://picsum.photos/1920/1080?random=1')",
            y: useTransform(progress, [0, 1], ['0%', '20%']),
          }}
        />
      </div>
      <div className="relative z-10 text-center text-white">
        <motion.h1
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="text-5xl md:text-7xl font-bold mb-4 text-shadow-lg"
        >
          
        </motion.h1>
        <motion.p
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          className="text-xl md:text-2xl mb-8 text-shadow"
        >
          
        </motion.p>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.8, delay: 0.4 }}
        >
          <ChevronDown size={48} className="animate-bounce" />
        </motion.div>
      </div>
    </motion.div>
  );
};

const DirectorInfo = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, y: 50 }}
      animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 50 }}
      transition={{ duration: 0.8 }}
      className="container mx-auto px-4 py-16"
    >
      <h2 className="text-3xl font-bold text-center text-purple-800 mb-8"></h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div className="bg-white rounded-lg shadow-xl p-6">
        <img src="https://picsum.photos/id/237/400/400" alt="Director" className="w-full h-64 object-cover rounded-lg mb-4" />
        <h3 className="text-2xl font-semibold text-purple-700 mb-4"></h3>
          <p className="text-gray-700"></p>
        </div>
        <div className="bg-white rounded-lg shadow-xl p-6">
          <h3 className="text-xl font-semibold text-purple-700 mb-4"></h3>
          <ul className="space-y-2">
            <InfoItem icon={Award} text="" />
            <InfoItem icon={Award} text="" />
            <InfoItem icon={BookOpen} text="" />
            <InfoItem icon={Briefcase} text="()" />
            <InfoItem icon={Users} text="" />
            <InfoItem icon={Users} text="" />
          </ul>
        </div>
      </div>
    </motion.div>
  );
};

const InfoItem = ({ icon: Icon, text }) => (
  <li className="flex items-center">
    <Icon className="text-purple-600 mr-2" />
    <span>{text}</span>
  </li>
);

const DepartmentIntro = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, y: 50 }}
      animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 50 }}
      transition={{ duration: 0.8 }}
      className="container mx-auto px-4 py-16 bg-purple-50"
    >
      <h2 className="text-3xl font-bold text-center text-purple-800 mb-8"></h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div className="bg-white rounded-lg shadow-xl p-6">
          <h3 className="text-xl font-semibold text-purple-700 mb-4"></h3>
          <p className="text-gray-700">
            
          </p>
        </div>
        <div className="bg-white rounded-lg shadow-xl p-6">
          <h3 className="text-xl font-semibold text-purple-700 mb-4"></h3>
          <ul className="space-y-2">
            <li className="flex items-center">
              <Award className="text-purple-600 mr-2" />
              <span></span>
            </li>
            <li className="flex items-center">
              <Users className="text-purple-600 mr-2" />
              <span></span>
            </li>
            <li className="flex items-center">
              <Briefcase className="text-purple-600 mr-2" />
              <span></span>
            </li>
            <li className="flex items-center">
              <BookOpen className="text-purple-600 mr-2" />
              <span></span>
            </li>
          </ul>
        </div>
      </div>
    </motion.div>
  );
};

const CoreConcepts = () => {
    const [selectedConcept, setSelectedConcept] = useState(null);
  
    const concepts = [
      {
        id: 1,
        name: "",
        icon: <Scale className="w-12 h-12" />,
        description: "",
        examples: ["", "", "", ""],
        importance: ""
      },
      {
        id: 2,
        name: "",
        icon: <HeartHandshake className="w-12 h-12" />,
        description: "",
        examples: ["", "", "", ""],
        importance: ""
      },
      {
        id: 3,
        name: "",
        icon: <Users className="w-12 h-12" />,
        description: "",
        examples: ["", "", "", ""],
        importance: ""
      },
      {
        id: 4,
        name: "",
        icon: <Globe className="w-12 h-12" />,
        description: "",
        examples: ["", "", "", ""],
        importance: ""
      },
      {
        id: 5,
        name: "",
        icon: <BookOpen className="w-12 h-12" />,
        description: "",
        examples: ["", "", ""],
        importance: ""
      }
    ];
  
    const ConceptCard = ({ concept }) => {
      const [isFlipped, setIsFlipped] = useState(false);
      const [showMore, setShowMore] = useState(false);
  
      return (
        <motion.div
          className="w-full h-[400px] perspective-1000"
          initial={false}
          animate={{ rotateY: isFlipped ? 180 : 0 }}
          transition={{ duration: 0.6, ease: "easeInOut" }}
          onClick={() => setIsFlipped(!isFlipped)}
        >
          <div className="w-full h-full relative">
            {/* Front of the card */}
            <motion.div
              className="w-full h-full absolute backface-hidden bg-gradient-to-br from-purple-400 to-indigo-600 rounded-xl p-6 flex flex-col items-center justify-center text-white"
              style={{ rotateY: 0 }}
            >
              {concept.icon}
              <h3 className="text-2xl font-bold mt-4 text-center">{concept.name}</h3>
              <p className="mt-2 text-center">{concept.description}</p>
            </motion.div>
  
            {/* Back of the card */}
            <motion.div
              className="w-full h-full absolute backface-hidden bg-white rounded-xl p-6 overflow-y-auto"
              style={{ rotateY: 180 }}
            >
              <h3 className="text-2xl font-bold text-purple-700 mb-4">{concept.name}</h3>
              <h4 className="font-semibold text-purple-600 mb-2"></h4>
              <ul className="list-disc list-inside mb-4">
                {concept.examples.map((example, index) => (
                  <li key={index} className="text-gray-700">{example}</li>
                ))}
              </ul>
              <AnimatePresence>
                {showMore && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: "auto" }}
                    exit={{ opacity: 0, height: 0 }}
                    transition={{ duration: 0.3 }}
                  >
                    <h4 className="font-semibold text-purple-600 mb-2"></h4>
                    <p className="text-gray-700">{concept.importance}</p>
                  </motion.div>
                )}
              </AnimatePresence>
              <button
                className="mt-4 text-purple-600 flex items-center"
                onClick={(e) => {
                  e.stopPropagation();
                  setShowMore(!showMore);
                }}
              >
                {showMore ? "" : ""}
                <ArrowRight className="ml-1 w-4 h-4" />
              </button>
            </motion.div>
          </div>
        </motion.div>
      );
    };
  
    return (
      <div className="container mx-auto px-4 py-16 bg-purple-50">
        <h2 className="text-4xl font-bold text-center text-purple-800 mb-12"></h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {concepts.map((concept) => (
            <ConceptCard key={concept.id} concept={concept} />
          ))}
        </div>
      </div>
    );
  };
const OrganizationStructure = () => {
    const [activeGroup, setActiveGroup] = useState(null);
    const [activeTab, setActiveTab] = useState('responsibilities');
  
    const groups = [
      {
        id: 1,
        name: "",
        icon: <Users className="w-8 h-8" />,
        description: "",
        responsibilities: [
          "",
          "",
          "",
          ""
        ],
        projects: [
          "",
          "",
          ""
        ]
      },
      {
        id: 2,
        name: "",
        icon: <HeartHandshake className="w-8 h-8" />,
        description: "",
        responsibilities: [
          "",
          "",
          "",
          ""
        ],
        projects: [
          "",
          "",
          ""
        ]
      },
      {
        id: 3,
        name: "",
        icon: <Globe className="w-8 h-8" />,
        description: "",
        responsibilities: [
          "",
          "",
          "",
          ""
        ],
        projects: [
          "",
          "",
          ""
        ]
      },
      {
        id: 4,
        name: "",
        icon: <BookOpen className="w-8 h-8" />,
        description: "",
        responsibilities: [
          "",
          "",
          "",
          ""
        ],
        projects: [
          "",
          "",
          ""
        ]
      },
      {
        id: 5,
        name: "",
        icon: <Scale className="w-8 h-8" />,
        description: "",
        responsibilities: [
          "",
          "",
          "",
          ""
        ],
        projects: [
          "",
          "",
          ""
        ]
      }
    ];
  
    const ItemCard = ({ item, icon }) => (
      <motion.div
        className="bg-white p-4 rounded-lg shadow-md"
        whileHover={{ scale: 1.05, boxShadow: "0 10px 15px -3px rgba(0, 0, 0, 0.1)" }}
      >
        <div className="flex items-center mb-2">
          {icon}
          <span className="ml-2 font-semibold text-purple-700">{item}</span>
        </div>
      </motion.div>
    );
  
    return (
      <div className="container mx-auto px-4 py-16 bg-gray-50">
        <h2 className="text-4xl font-bold text-center text-purple-800 mb-12"></h2>
        <div className="flex flex-wrap justify-center gap-4 mb-12">
          {groups.map((group) => (
            <motion.button
              key={group.id}
              className={`px-6 py-3 rounded-full text-lg font-semibold transition-colors duration-300 ${
                activeGroup === group.id
                  ? 'bg-purple-600 text-white shadow-lg'
                  : 'bg-white text-purple-700 hover:bg-purple-100'
              }`}
              onClick={() => setActiveGroup(group.id)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <span className="flex items-center">
                {React.cloneElement(group.icon, { className: 'w-6 h-6 mr-2' })}
                {group.name}
              </span>
            </motion.button>
          ))}
        </div>
        <AnimatePresence mode="wait">
          {activeGroup && (
            <motion.div
              key={activeGroup}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
              className="bg-white rounded-lg shadow-xl p-8 max-w-4xl mx-auto"
            >
              {groups.find(g => g.id === activeGroup) && (
                <>
                  <div className="flex items-center mb-6">
                    <div className="text-purple-600 mr-4">
                      {groups.find(g => g.id === activeGroup).icon}
                    </div>
                    <h3 className="text-3xl font-bold text-purple-700">{groups.find(g => g.id === activeGroup).name}</h3>
                  </div>
                  <p className="text-gray-700 text-lg mb-6">{groups.find(g => g.id === activeGroup).description}</p>
                  
                  <div className="flex space-x-4 mb-6">
                    <button
                      className={`px-4 py-2 rounded-full ${activeTab === 'responsibilities' ? 'bg-purple-600 text-white' : 'bg-purple-100 text-purple-600'}`}
                      onClick={() => setActiveTab('responsibilities')}
                    >
                      
                    </button>
                    <button
                      className={`px-4 py-2 rounded-full ${activeTab === 'projects' ? 'bg-purple-600 text-white' : 'bg-purple-100 text-purple-600'}`}
                      onClick={() => setActiveTab('projects')}
                    >
                      
                    </button>
                  </div>
                  
                  <AnimatePresence mode="wait">
                    <motion.div
                      key={activeTab}
                      initial={{ opacity: 0, x: 20 }}
                      animate={{ opacity: 1, x: 0 }}
                      exit={{ opacity: 0, x: -20 }}
                      transition={{ duration: 0.3 }}
                    >
                      {activeTab === 'responsibilities' ? (
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          {groups.find(g => g.id === activeGroup).responsibilities.map((resp, index) => (
                            <ItemCard key={index} item={resp} icon={<CheckCircle className="w-5 h-5 text-green-500" />} />
                          ))}
                        </div>
                      ) : (
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          {groups.find(g => g.id === activeGroup).projects.map((project, index) => (
                            <ItemCard key={index} item={project} icon={<Zap className="w-5 h-5 text-yellow-500" />} />
                          ))}
                        </div>
                      )}
                    </motion.div>
                  </AnimatePresence>
                </>
              )}
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    );
  };

const BusinessIntroduction = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, y: 50 }}
      animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 50 }}
      transition={{ duration: 0.8 }}
      className="container mx-auto px-4 py-16"
    >
      <h2 className="text-3xl font-bold text-center text-purple-800 mb-8"></h2>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        <BusinessCard
          title=""
          description=""
          icon={<Award size={48} className="text-purple-600" />}
        />
        <BusinessCard
          title=""
          description=""
          icon={<BookOpen size={48} className="text-purple-600" />}
        />
        <BusinessCard
          title=""
          description="App"
          icon={<Briefcase size={48} className="text-purple-600" />}
        />
        <BusinessCard
          title=""
          description=""
          icon={<Users size={48} className="text-purple-600" />}
        />
        <BusinessCard
          title=""
          description=""
          icon={<BookOpen size={48} className="text-purple-600" />}
        />
        <BusinessCard
          title=""
          description=""
          icon={<Users size={48} className="text-purple-600" />}
        />
      </div>
    </motion.div>
  );
};

const BusinessCard = ({ title, description, icon }) => (
  <motion.div
    whileHover={{ scale: 1.05 }}
    whileTap={{ scale: 0.95 }}
    className="bg-white rounded-lg shadow-xl p-6 flex flex-col items-center text-center"
  >
    {icon}
    <h3 className="text-xl font-semibold text-purple-700 mt-4 mb-2">{title}</h3>
    <p className="text-gray-700">{description}</p>
  </motion.div>
);

const Timeline = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });

  const timelineEvents = [
    { year: 2020, event: "" },
    { year: 2021, event: "" },
    { year: 2022, event: "App" },
    { year: 2023, event: "" },
    { year: 2024, event: "" }
  ];

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, y: 50 }}
      animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 50 }}
      transition={{ duration: 0.8 }}
      className="container mx-auto px-4 py-16 bg-purple-50"
    >
      <h2 className="text-3xl font-bold text-center text-purple-800 mb-8"></h2>
      <div className="relative">
        <div className="absolute top-0 left-1/2 w-1 h-full bg-purple-300 transform -translate-x-1/2"></div>
        {timelineEvents.map((event, index) => (
          <TimelineEvent key={index} year={event.year} event={event.event} isLeft={index % 2 === 0} />
        ))}
      </div>
    </motion.div>
  );
};

const TimelineEvent = ({ year, event, isLeft }) => (
  <motion.div
    initial={{ opacity: 0, x: isLeft ? -50 : 50 }}
    whileInView={{ opacity: 1, x: 0 }}
    viewport={{ once: true }}
    transition={{ duration: 0.5 }}
    className={`flex items-center mb-8 ${isLeft ? 'justify-start' : 'justify-end'}`}
  >
    <div className={`w-5/12 ${isLeft ? 'text-right pr-4' : 'text-left pl-4'}`}>
      <div className="bg-white rounded-lg shadow-xl p-4">
        <h3 className="text-xl font-semibold text-purple-700">{year}</h3>
        <p className="text-gray-700">{event}</p>
      </div>
    </div>
    <div className="w-2/12 flex justify-center">
      <div className="w-4 h-4 bg-purple-500 rounded-full"></div>
    </div>
    <div className="w-5/12"></div>
  </motion.div>
);

export default AboutPage;
